.projects {
  width: 100%;
  background-color: white;
  padding-top: 50px;
  padding-bottom: 100px;
  border-radius: 0px 0px 100px 100px;
  box-shadow: 0px 5px 5px #00000050;
}

.projects::before {
  content: "";
  display: flex;
  position: relative;
  top: -200px;
  width: 100%;
  height: 150px;
  box-shadow: 0px 5px 5px #00000050;
  transform-origin: left;
  transform: skewY(4deg);
  z-index: 0;
}

.projects__title {
  font-size: 40px;
  font-weight: 300;
  text-shadow: 0px 2px 5px 00000050;
  margin-top: -150px;
  margin-left: 50px;
}

.projects__contain {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: 50px;
}

@media only screen and (max-width: 768px) {
  .projects__contain {
    margin: 20px;
  }
}
